
.dialogs {
    &__name {
        display: grid;
        grid-template-columns: 2fr 2fr;
        width: 100px;
        font-size: 20px;
        margin-bottom: 30px;
        color: gray;
        list-style-type: none;
        align-items: center;
        &_active {
            font-size: 23px;
            color: #551111;
            transition: 0.3s all; }
        &_avatar {
            display: inline-block;
            width: 50px;
            margin-right: 25px; } } }
